import React, { useContext, useState } from "react";
import "./budget.scss";
import { H, HDown, LevelContext } from "components/heading";
import useBudgetReducer from "./useBudgetReducer";
import Carrot from "components/carrot";
import Visualization from "components/visualization";

export const formatDollars = new Intl.NumberFormat("en-US", {
  style: "currency",
  currency: "USD",
});

const Budget = ({ data, state, setValue }) => {
  const level = useContext(LevelContext);
  const keys = Object.keys(data).sort(sortByTotal);
  const { changes } = state;

  function sortByTotal(a, b) {
    const aval = getTotal(data[a]);
    const bval = getTotal(data[b]);

    if (aval > bval) {
      return -1;
    } else if (aval < bval) {
      return 1;
    } else {
      return 0;
    }
  }

  if (keys.length === 1 && !Array.isArray(data[keys[0]])) {
    return <Budget data={data[keys[0]]} state={state} setValue={setValue} />;
  }
  return (
    <HDown>
      <div style={{ marginLeft: `${level > 1 ? 2 : 0}rem` }}>
        {keys.map((key) => {
          if (Array.isArray(data[key])) {
            return (
              <div
                key={key}
                className="PoliceBudget-lineitem PoliceBudget-lineitem--final"
              >
                <H>{key}</H>
                <Adjust
                  value={changes[key]}
                  onChange={setValue(data[key][0].uid)}
                />
                <div>{formatDollars.format(data[key][0].total)}</div>
              </div>
            );
          } else {
            const uid = bottomUid(data[key]); //this is innacurate for large cats, pulling first
            const nextLevel = nextLevelDown(data[key]);
            const before = getTotal(data[key]);
            const after = getTotal(data[key], changes);

            const nextIsParent =
              data[keys[keys.indexOf(key) + 1]] &&
              nextLevelDown(data[keys[keys.indexOf(key) + 1]]);

            return (
              <LineItem
                key={key}
                title={key}
                nextLevel={nextLevel}
                nextIsParent={nextIsParent}
                value={changes[uid]}
                setValue={setValue(uid)}
                before={before}
                after={after}
              >
                {nextLevel && (
                  <div>
                    <Budget
                      data={nextLevel}
                      state={state}
                      setValue={setValue}
                    />
                  </div>
                )}
              </LineItem>
            );
          }
        })}
      </div>
    </HDown>
  );
};

const LineItem = ({
  nextLevel,
  nextIsParent,
  title,
  value,
  setValue,
  before,
  after,
  children,
}) => {
  const [open, setOpen] = useState(false);
  const toggleOpen = () => setOpen(!open);
  return (
    <React.Fragment>
      <div
        className={`PoliceBudget-lineitem ${
          !nextLevel ? "PoliceBudget-lineitem--final" : ""
        } ${nextIsParent ? "PoliceBudget-lineitem--last" : ""}`}
      >
        {nextLevel ? (
          <button
            className={`PoliceBudget-lineitem-toggle ${open ? "is-open" : ""}`}
            onClick={toggleOpen}
          >
            <Carrot />
            <H>{title}</H>
          </button>
        ) : (
          <H>{title}</H>
        )}
        <div>{!nextLevel && <Adjust value={value} onChange={setValue} />}</div>
        <div>{formatDollars.format(before)}</div>
        <div>{formatDollars.format(after)}</div>
      </div>
      {open && children}
    </React.Fragment>
  );
};
const Adjust = ({ value, onChange }) => {
  return (
    <input
      type="number"
      value={value || 0}
      increment={1}
      max={100}
      min={-100}
      onChange={(e) => onChange(+e.target.value)}
    />
  );
};

function bottomUid(data) {
  if (Array.isArray(data)) {
    return data[0].uid;
  } else {
    return bottomUid(data[Object.keys(data)[0]]);
  }
}

function nextLevelDown(data) {
  if (Array.isArray(data)) {
    return null;
  }
  const keys = Object.keys(data);
  if (keys.length === 1) {
    return nextLevelDown(data[keys[0]]);
  }
  return data;
}

export const getTotal = (data, changes) => {
  if (Array.isArray(data)) {
    return sumRows(data);
  }
  return Object.values(data).reduce((r, d) => {
    return r + (Array.isArray(d) ? sumRows(d) : getTotal(d, changes));
  }, 0);

  function sumRows(rows) {
    return rows.reduce((r, d) => {
      const co = changes && changes[d.uid] ? 1 - changes[d.uid] / 100 : 1;
      return r + +d.total * co;
    }, 0);
  }
};

export default ({ cityLabel, city, data }) => {
  const { state, setValue } = useBudgetReducer(city);

  const before = getTotal(data);
  const after = getTotal(data, state.changes);
  return (
    <div className="PoliceBudget">
      <div className="PoliceBudget-main">
        <div className="PoliceBudget-title">
          <h1>{cityLabel}</h1>
        </div>
        <div className="PoliceBudget-lineitem PoliceBudget-header">
          <div>Category</div>
          <div>% Reduction</div>
          <div>Before</div>
          <div>After</div>
        </div>
        <div className="PoliceBudget-budget">
          <Budget data={data} state={state} setValue={setValue} />
        </div>
        <div className="PoliceBudget-lineitem PoliceBudget-total">
          <div>Total</div>
          <div></div>
          <div>{formatDollars.format(before)}</div>
          <div>{formatDollars.format(after)}</div>
        </div>
      </div>
      <div>
        <div className="PoliceBudget-total">{formatDollars.format(before)}</div>
        <div className="PoliceBudget-total">{formatDollars.format(after)}</div>
      </div>
      {/* <Visualization data={data} /> */}
    </div>
  );
};
